<template>
    <div class="listId3">
        <div>
            <router-link :to="{name:'GoodsDetail',query:{goodsId:'1b5c1c46-0e89-482b-a3c4-8dc7fce55fb0'}}">
                <img src="https://wochu.oss-cn-hangzhou.aliyuncs.com/upload/671c1a6e-97ec-4961-b7c6-96353411295a.jpg" />
            </router-link>
        </div>
        <ul class="clear">
            <router-link tag="li" v-if="item.parentId==3" v-for="(item,index) in list" to="" key="index">
                <a href="javascript:;">
                    <img :src="item.checkicon" />
                    <p>{{item.name}}</p>
                </a>
            </router-link>
        </ul>
    </div>
</template>
<script>
import listJson from "../../json/list.json";
export default {
    name: "listId2",
    data() {
        return {
            list: listJson.data
        }
    }
}
</script>
<style lang="less">
.listId3 {
    &>div a{
        display: inline-block;
        width:100%;
        img{
            border:0 auto;
            width:100%;
            height:auto;
        }
    }
}
</style>





