<template>
    <div>
        <div class="my_header">
            <div class="mytBox">
                <img src="http://wmall.wochu.cn/h5/personal/css/images/center_image_user@3x.png"/>
                <p>昵称</p>
                <h4>{{userName}}</h4>
            </div>
            <router-link :to="{name:'Set'}" class="mySet iconfont icon-shezhi"></router-link>
            <div class="jifen">
                <a href="javascript:;"><dt>0</dt><dl>积分</dl></a>
                <a href="javascript:;"><dt>0</dt><dl>优惠券</dl></a>
            </div>
        </div>
        <div class="my_main">
            <ul class="stayPay">
                <router-link tag="li" v-for="(item,index) in tagList.list1" to="" :key="index">
                    <a href="javascript"><div :class="'iconfont '+item.className"></div><p>{{item.text}}</p></a>
                </router-link>
            </ul>
            <div class="order"><router-link  class="plr3430" to=""><i class="iconfont icon-quanbudingdan"></i><span>全部订单</span><b>查看全部订单</b><em>></em></router-link></div>
            <ul class="myBulk">
                <router-link tag="li" v-for="(item,index) in tagList.list2" to="" :key="index">
                    <a href="javascript"  class="plr3430"><i :class="'iconfont '+item.className"></i><span>{{item.text}}</span><em>></em></a>
                </router-link>
            </ul>
            <ul class="wochuAbout">
                <router-link tag="li" v-for="(item,index) in tagList.list3" to="" :key="index">
                    <a href="javascript"  class="plr3430"><i :class="'iconfont '+item.className"></i><span>{{item.text}}</span><em>></em></a>
                </router-link>
            </ul>
        </div>
        <Bottom></Bottom>
        <div class="alertBox" ref="alertBox" v-show="alertState">{{alertText}}</div>
    </div>
</template>
<script>
import VueRouter from "vue-router";
var router = new VueRouter();
import Bottom from "./com/Footer";
export default {
    name: "my",
    data() {
        return {
            userName: null,
            alertText: null,//警告框的文本内容
            alertState: false,//警告框显示隐藏状态
            tagList:{
                list1:[
                    {className:"icon-daifukuan",text:"待付款",name:"StayPay"},
                    {className:"icon-daishouhuo",text:"待收货",name:"StayGoods"},
                    {className:"icon-daipingjia1",text:"待评价",name:"StayComm"},
                    {className:"icon-tuihuoshouhou",text:"退款/售后",name:"StayBack"}
                ],
                list2:[
                    {className:"icon-shoucang",text:"我的团购",name:"MyBulk"},
                    {className:"icon-wodedizhi",text:"我的地址",name:"MyAddress"},
                    {className:"icon-shoucang",text:"我的收藏",name:"MyCollect"},
                    {className:"icon-wode3",text:"邀请好友",name:"MyFriends"},
                ],
                list3:[
                    {className:"icon-guanyu",text:"关于我厨",name:"WochuAbout"},
                    {className:"icon-help",text:"帮助中心",name:"WochuHelp"},
                    {className:"icon-daipingjia",text:"意见反馈",name:"WochuFeedBack"},
                ]
            }
        }
    },
    components:{
        Bottom
    },
    created(){
        var userName = localStorage.getItem("userName")
        if (!userName) {
            var that = this;
            this.alertState = true;
            this.alertText = "您还未登录，请先登录";
            setTimeout(function () {
                that.alertState = false;
                router.push({
                    path: "/login"
                });
            }, 1000)

        } else {
            // console.log(userName);
            this.userName = userName;
        }
    },
    // beforeRouteEnter: function (to, from, next) {
    //     var userName = to.query.userName;
    //     next(function (vm) {
    //         if (!userName) {
    //             var that = vm;
    //             vm.alertState = true;
    //             vm.alertText = "您还未登录，请先登录";
    //             setTimeout(function () {
    //                 that.alertState = false;
    //                 router.push({
    //                     path: "/login"
    //                 });
    //             }, 1000)

    //         } else {
    //             // console.log(userName);
    //             vm.userName = userName;
    //         }
    //     })
    // }
}
</script>



