<template>
  <div>
      <div v-if="!loginState" class="cart_isLogin">您还没登录，请先<a @click="toLogin">登录</a></div>
      <div v-if="loginState">
          <header>
              <i class="iconfont icon-back" @click="goBack"></i>
              <div class="haddress">
                  <router-link to="Address">
                      <i class="iconfont icon-h-dizhi"></i><span>请填写地址</span><i class="iconfont icon-h-xia"></i>
                  </router-link>
              </div>
              <div class="hsearch">
                  <a @click="change">{{changeText}}</a>
              </div>
          </header>
          <div class="cart_main">
              <div></div>
              <CartItem v-for="(item , index) in goodsList" :goods="item" :key="index" @carUpdate="carUpdate" :userName="userName"></CartItem>
          </div>
          <div class="cart_count">
              <div class="select_box"><i class="iconfont icon-duigou1"></i></div>
              <div class="goodsPay" v-if="changeState">
                  <p>商品金额：<span>{{priceCount | currency}}</span></p>
                  <dl>不含运费</dl>
              </div>
              <div class="goodsPay" v-else>
                  <a><p>清空购物车</p></a>
              </div>
              <div class="account">{{accountText}}<span v-if="accountText == '结算'">({{numCount}})</span></div>
          </div>
      </div>
      <Bottom></Bottom>
  </div>
</template>
<script>
import VueRouter from "vue-router";
var router = new VueRouter();
import CartItem from "./com/CartItem";
import Bottom from "./com/Footer";
export default {
  name:"cart",
  data(){
    return {
      userName:null,
      loginState:true,//登录状态
      goodsList:[],//该用户的商品数组
      changeState:true,//点编辑变完成，点完成变编辑的开关
      changeText:"编辑",//点击编辑时改变成完成
      //allPrice:0,//总价
      //allNum:0,//所有商品数量
      accountText:"结算"//结算or删除
    }
  },
  methods:{
    goBack(){
          history.go(-1);
    },
    toLogin(){
      router.push({
          path:"/login",
          query:{redirect:'/cart'}
      });
    },
    change(){
      if(this.changeState){
          this.changeText = "完成";
          this.accountText = "删除";
          this.changeState = false;
      }else{
          this.changeText = "编辑";
          this.accountText = "结算";
          this.changeState = true;
      }
    },
    carUpdate() {
        var that = this;
        this.$http.get("http://bxu2359550159.my3w.com/wochuphp/myshoppingcart.php?userId="+this.userName).then(function(res){
            // console.log(res);
            // that.goodsList = res.body;//res.body返回的是是一个数组
            that.goodsList = res.body.map(function (item) {
                var obj = {
                    src: item[7],
                    name: item[4],
                    price: item[5]*1,
                    num: item[3]* 1,
                    goodsId: item[2]
                };
                return obj;
            });
            // for(var i=0;i<res.body.length;i++){
            //     that.allNum += res.body[i][3]*1;
            //     that.allPrice += res.body[i][5]*res.body[i][3]*1;
            // }
            
        })
    },
    remove(){
        var that = this;
        this.$http.get("http://bxu2359550159.my3w.com/wochuphp/deletehoppingcart.php?userId="+this.userName+"&goodId="+this.goods.goodsId).then(function(res){
            // var result = res.body;
            that.$emit("carUpdate");
        })
    }
  },
  computed: {
      numCount: function () {//所有商品数量
          var count = 0;
          this.goodsList.map(function (obj) {
              count += obj.num;
          });
          return count;
      },
      priceCount: function () {//商品总价
          var price = 0;
          this.goodsList.map(function (obj) {
              price += obj.price * obj.num;
          });

          return price;
      }
  },
  filters: {
        currency: function (input, sign, num) {
            var nNum = num ? num : 2;
            var nSign = sign ? sign : "￥";
            return nSign + (input * 1).toFixed(nNum);
        }
  },
  created(){
    var userName = localStorage.getItem("userName");
    this.userName = userName;
    if(!userName){
        this.loginState = false;
    }else{
        //若有用户登录，则显示该用户的购物车信息
        this.loginState = true;
        var that = this;
        this.$http.get("http://bxu2359550159.my3w.com/wochuphp/myshoppingcart.php?userId="+this.userName).then(function(res){
            // console.log(res);
            // that.goodsList = res.body;//res.body返回的是是一个数组
            that.goodsList = res.body.map(function (item) {
                var obj = {
                    src: item[7],
                    name: item[4],
                    price: item[5]*1,
                    num: item[3]* 1,
                    goodsId: item[2]
                };
                return obj;
            });
            // for(var i=0;i<res.body.length;i++){
            //     that.allNum += res.body[i][3]*1;
            //     that.allPrice += res.body[i][5]*res.body[i][3]*1;
            // }
            
        })
    }
  },
  components:{
    CartItem,
    Bottom
  }
}
</script>
<style lang="less" scoped>
    header{
        width:100%;
        height:0.90rem;
        position:fixed;
        z-index:3;
        top:0;
        left:0;
        padding:0.26rem 0.32rem;
        box-sizing: border-box;
        background:white;
        border-bottom:0.01rem solid #b2b2b2; 
        &>i{
            font-size: 0.3rem;
            position: absolute;
            top: 0.30rem;
            left: 0.25rem;
        }
        .haddress{
            width:2.25rem;
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
            a{
                color:#000;
                font-size: 0.27rem;
                span{
                    margin:0 0.11rem;
                }
                i{
                    font-size: 0.27rem;
                }
            }
        }
        .hsearch{
            position: absolute;
            top:50%;
            right:0.17rem;
            transform: translateY(-50%);
          a{
            font-size: 0.24rem;
            color:#000;
          }
        }
    }
    .cart_isLogin{
        width:5rem;
        height:2rem;
        color:#ec6929;
        font-size: 0.3rem;
        position: absolute;
        top:2rem;
        left:50%;
        transform: translateX(-50%);
        text-align: center;
        line-height: 2rem;
        a{
          font-size: 0.3rem;
        }
    }
</style>





