<template>
    <div class="cart_item">
        <div class="select_box"><i class="iconfont icon-duigou1"></i></div>
        <div class="img_box">
            <img :src="goods.src" />
        </div>
        <div class="content_box">
            <h3>{{goods.name}}</h3>
            <p class="item_price">
                <span>{{goods.price | currency}}</span>
            </p>
            <div class="item_num">
                <a class="lessBtn" href="javascript:;" @click="del">-</a>
                <input type="text" v-model.number="goods.num" />
                <a class="addBtn" href="javascript:;" @click="add">+</a>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  name:"cartItem",
  props:{
      goods:{
          type:Object,
          required:true
      },
      userName:{
          type:String,
          required:false
      }
  },
  data(){
      return {
          isOper:true
      }
  },
  methods:{
        add(){
            if(this.isOper){
                this.goods.num++;
                this.isOper = false;
            }
        },
        del(){
            if(this.goods.num > 1 && this.isOper){
                this.goods.num --;
                this.isOper = false;
            }
        }
    },
    filters: {
        currency: function (input, sign, num) {
            var nNum = num ? num : 2;
            var nSign = sign ? sign : "￥";
            return nSign + (input * 1).toFixed(nNum);
        }
    },
    updated:function(){
        var that = this;
        this.$http.get("http://bxu2359550159.my3w.com/wochuphp/updateshoppingcart.php?userId="+this.userName+"&goodId="+this.goods.goodsId+"&num="+this.goods.num).then(function(res){
            if(res.body==0){
                that.$emit("carUpdate");
            }
            that.isOper = true;
        })
    }
}
</script>




