<template>
    <footer>
        <nav>
            <ul>
                <router-link tag="li" active-class="active" :to="nav.path" v-for="(nav , index) in footerList" :key="index">
                    <a href="javascript:;">
                        <div :class="['iconfont' , nav.class]"></div>
                        <p>{{nav.text}}</p>
                    </a>
                </router-link>
            </ul>
        </nav>
    </footer>
</template>
<script>
export default {
    name: "bottom",
    data() {
        return {
            footerList: [
                { text: "首页", path: "/home", class: "icon-shouyeicon2" },
                { text: "分类", path: "/list", class: "icon-fenlei" },
                { text: "购物车", path: "/cart", class: "icon-gouwuche" },
                { text: "我的", path: "/my", class: "icon-wode3" }
            ]
        }
    }
}
</script>


