<template>
    <div class="homeContainer">
        <ul class="ul1">
            <li v-for="(item,index) in recommendedContent" key="index" >
                <a :href="item.source">
                    <img :src="item.imgUrl"/>
                </a>
            </li>
        </ul>
        <div class="click">
            <router-link :to="{path:'/iframeClick',query:{src:'http://wmall.wochu.cn/h5/activityTemplate/html/activityPage.html?activityId=176'}}">
                <h6>点击查看</h6>
                <p><i class="iconfont icon-cloud-clock"></i>48h自助退换货<i class="iconfont icon-duigou"></i>48h保价退差价</p>
            </router-link>
        </div>
        <ul class="ul2">
            <li v-for="(item,index) in acts" key="index" >
                <router-link v-if="item.items[0].source.length < 1" :to="{name:'IframeClick',query:{src:item.items[0].source}}" class="special">
                    <img :src="item.items[0].imgUrl"/>
                </router-link>
                <router-link v-else :to="{name:'IframeClick',query:{src:item.items[0].source}}">
                    <img :src="item.items[0].imgUrl"/>
                </router-link>
                <!--<router-link :to="{path:'/iframeClick',query:{src:item.items[0].source}}">
                    <img :src="item.items[0].imgUrl"/>
                </router-link>-->
            </li>
        </ul>
    </div>
</template>
<script>
var homeJson = require("../../json/home.json");
export default {
    name:"homeContainer",
    props:{
        recommendedContent:{
            type:Array,
            required : true
        }
    },
    data(){
        return {
            acts:homeJson.acts,
            src:null
        }
    },
    // beforeMount:function(){
    //     for(var i = 0;i< acts.length;i++){
    //         if(acts[i].items[0].source.length < 1){
                
    //         }

    //     }
    // }
}
</script>
<style lang="less">
    // .special{
    //     width:100%;
    //     height:1.11rem;
    // }
</style>




