import Vue from 'vue'
import Router from 'vue-router'


import Home from '@/components/Home';
import List from '@/components/List';
import IframeClick from '@/components/IframeClick';
import ListDetail from '@/components/ListDetail';
import GoodsDetail from '@/components/GoodsDetail';
import Cart from '@/components/Cart';
import My from '@/components/My';

import Register from '@/components/Register';
import Login from '@/components/Login';
import SetPage from '@/components/Set'
import DemoRefrash from '@/components/DemoRefrash';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: "Home",
      component: Home
    },
    {
      path: '/list',
      name: "List",
      component: List
    },
    {
      path: '/iframeClick',
      name: "IframeClick",
      component: IframeClick
    },
    {
      path: '/listDetail',
      name: 'ListDetail',
      component: ListDetail
    },
    {
      path: '/goodsDetail',
      name: 'GoodsDetail',
      component: GoodsDetail
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart
    },
    {
      path: '/my',
      name: 'My',
      component: My
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/set',
      name: 'Set',
      component: SetPage
    },
    {
      path: '/demoRefrash',
      component: DemoRefrash
    }
  ]
})



