<template>
    <div>
        <div class="list_header">
            <input type="text" placeholder="列表页由‘时令蔬果’进入，仅限此搜索" />
            <i class="iconfont icon-h-search"></i>
        </div>
        <div class="list_main">
            <nav>
                <ul>
                    <li id="tj" @click="menuClick('tj')" :class="{'active': className == 'tj'}">
                        <p>推荐</p>
                    </li>
                    <li id="2" @click="menuClick(2)" :class="{'active': className == '2'}">
                        <p>我厨优选</p>
                    </li>
                    <li id="3" @click="menuClick(3)" :class="{'active': className == '3'}">
                        <p>餐馆名菜</p>
                    </li>
                    <li id="1" @click="menuClick(1)" :class="{'active': className == '1'}">
                        <p>全部净菜</p>
                    </li>
                    <li id="4" @click="menuClick(4)" :class="{'active': className == '4'}">
                        <p>儿童专区</p>
                    </li>
                    <li id="6" @click="menuClick(6)" :class="{'active': className == '6'}">
                        <p>蔬菜</p>
                    </li>
                    <li id="7" @click="menuClick(7)" :class="{'active': className == '7'}">
                        <p>水果</p>
                    </li>
                    <li id="8" @click="menuClick(8)" :class="{'active': className == '8'}">
                        <p>肉禽蛋</p>
                    </li>
                    <li id="9" @click="menuClick(9)" :class="{'active': className == '9'}">
                        <p>水产海鲜</p>
                    </li>
                    <li id="10" @click="menuClick(10)" :class="{'active': className == '10'}">
                        <p>乳饮西点</p>
                    </li>
                    <li id="5" @click="menuClick(5)" :class="{'active': className == '5'}">
                        <p>点心速食</p>
                    </li>
                    <li id="11" @click="menuClick(11)" :class="{'active': className == '11'}">
                        <p>粮油副食</p>
                    </li>
                </ul>
            </nav>
            <section>
                <component :is="listPanel"></component>
            </section>
        </div>
        <Bottom></Bottom>
    </div>
</template>
<script>
import Bottom from "./com/Footer";
import ListTj from "./com/ListTj";
import ListId2 from "./com/ListId2";
import ListId3 from "./com/ListId3";
import ListId1 from "./com/ListId1";
import ListId4 from "./com/ListId4";
import ListId6 from "./com/ListId6";
import ListId7 from "./com/ListId7";
import ListId8 from "./com/ListId8";
export default {
    name:"list",
    data(){
        return {
            listPanel:"ListTj",
            className:"tj"
        }
    },
    components: {
        Bottom,
        ListTj,
        ListId2,
        ListId3,
        ListId1,
        ListId4,
        ListId6,
        ListId7,
        ListId8,
    },
    methods:{
        menuClick:function(i){
            switch(i){
                case "tj":
                    {this.listPanel = "ListTj";this.className="tj"}
                    break;
                case 2:
                    {this.listPanel = "ListId2";this.className="2"}
                    break;
                case 3:
                    {this.listPanel = "ListId3";this.className="3"}
                    break;
                case 1:
                    {this.listPanel = "ListId1";this.className="1"}
                    break;
                case 4:
                    {this.listPanel = "ListId4";this.className="4"}
                    break;
                case 6:
                    {this.listPanel = "ListId6";this.className="6"}
                    break;
                case 7:
                    {this.listPanel = "ListId7";this.className="7"}
                    break;
                case 8:
                    {this.listPanel = "ListId8";this.className="8"}
                    break;
            }
        }
    }
}
</script>







