// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import Resource from "vue-resource";//ajax请求的插件 因为jquery插件容量太大
Vue.use(Resource);

//可能vue版本不支持，没效果
import verify from 'vue-verify-pop';//自带气泡提示的校验插件 npm vue-verify-pop -S
Vue.use(verify);
// verify.errMsg = ""
// verify.addRule('myRule',(v)=>{return '校验不通过'})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



