<template>
    <div>
        <iframe :src="src"></iframe>
        <Bottom></Bottom>
    </div>
</template>
<script>
import Bottom from "./com/Footer";
export default {
    name: "iframeClick",
    data() {
        return {
            src: {
                type: String,
                required: true
            }
        }
    },
    components: {
        Bottom
    },
    beforeRouteEnter: function (to, from, next) {
        next(function(vm){
            vm.src = to.query.src;
        });
    }
}
</script>
<style lang="less">
iframe {
    width: 100%;
    height: 20rem;
    padding-bottom: 0.98rem;
}
</style>




