<template>
  <div class="swiper-slide">
      <router-link v-if="(goods) instanceof Object" :to="{name:'IframeClick',query:{src:goods.action.data.target}}">
        <img :src="goods.picUrl" />
      </router-link>
      <router-link v-else to="">
        <img :src="goods" />
      </router-link>
  </div>
</template>
<script>
export default {
  name : "bannerItem",
  props : ["goods"],
  // mounted:function(){
  //   console.log(typeof(this.goods));
  //   if(("222") instanceof String){
  //     console.log("2222");
  //   }
  // }
}
</script>




