<template>
    <div>
        <div class="reglog_header">
            <h3>设置</h3>
            <i class="iconfont icon-back" @click="goBack"></i>
        </div>
        <div class="set_person">个人资料</div>
        <div class="set_logOut" @click="logOut">退出账户</div>
    </div>
</template>

<script>
import VueRouter from "vue-router";
var router = new VueRouter();
export default {
    name: "setPage",
    methods: {
        goBack(){
          history.go(-1);
        },
        logOut() {
            localStorage.clear("username");
            router.push("/home");
        }
    }
}
</script>
<style lang="less">
    .set_logOut{
        width:100%;
        background: #ec6929;
        color:white;
        font-size: 0.3rem;
        padding:0.2rem;
    }
</style>



