<template>
  <div>
    <div v-for="(item,index) in goodsList" v-if="item.goodsGuid == goodsId" :key="index">
      <div class="gh_btn">
          <div class="backBtn iconfont icon-fanhui1" @click="goBack"></div>
          <div class="shareBtn iconfont icon-fenxiang"></div>
          <div class="alertBox" ref="alertBox" v-show="alertState">{{alertText}}</div>
      </div>
      <Banner :bannerList="item.goodsAttributeImg" :swiperClass="swiperClass"></Banner>
      <div class="goodsDetail_main">
        <div class="item">
          <h3>{{item.goodsName}}</h3>
          <p>{{item.descrip}}</p>
          <div class="priceOperat"><span class="red">￥{{item.price}}</span><del>￥{{item.marketPrice}}</del>
            <div class="countOperat">
              <a @click="lessFun">-</a><span class="count">{{count}}</span><a @click="addFun">+</a>
            </div>
          </div>
        </div>
        <div class="comment"><a>商品评价<i>&gt;</i></a></div>
        <div class="detail">
          <p><strong>品名：</strong><span>{{item.goodsName}}</span></p>
          <p><strong>产地：</strong><span class="w198">{{item.origin1}}</span><strong>保质期：</strong><span>{{item.shelfLife}}</span></p>
          <p><strong>规格：</strong><span class="w198">{{item.specification}}</span><strong>存储条件：</strong><span>{{item.storageCondition}}</span></p>
        </div>

        <div class="description" v-for="(str,index) in item.descriptionDetail" :key="index">
          <img :src="str"/>
        </div>
      </div>

      <div class="goodsDetail_footer">
          <router-link :to="{name:'Cart'}" class="iconfont icon-gouwuche"><span>{{numCount}}</span></router-link>
          <span class="iconfont icon-shoucang" :class="{'red':isOpen}" @click="classFun"></span>
          <button @click="inCart(item)">加入购物车</button>
      </div>
    </div>
    
  </div>
</template>
<script>
import List from "../json/fruit.json";
import Banner from "./com/Banner";

import VueRouter from "vue-router";
var router = new VueRouter();

export default {
  name: "goodsDetail",
  data() {
    return {
      swiperClass: "goodsBanner",
      goodsList: [],
      tagName: null,
      goodsId: null,
      isOpen:false,//class开关
      count:1,//点击+/-的商品数量
      alertText: null,//警告框的文本内容
      alertState:false,//警告框显示隐藏状态
      userName:null,
      numCount:0
    }
  },
  methods:{
    classFun(){
      this.isOpen = !this.isOpen;
    },
    goBack(){
      history.go(-1);
    },
    addFun(){//增加商品数量
        this.count ++;
    },
    lessFun(){//减少商品数量
        this.count --;
        if(this.count <= 1){
          this.count = 1;
        }
    },
    inCart(obj){
        // alert("加入购物车成功");
        var userName = localStorage.getItem("userName");
        if(!userName){
            router.push({
                path:"/login",
                query:{redirect:'/goodsDetail?tagName='+this.tagName+'&goodsId='+this.goodsId}
            });
        }else{
            var userId = userName;
            var goodId = obj.goodsGuid;
            var goodName = obj.goodsName;
            var num = this.count;
            var goodPrice = obj.price;
            var oldPrice = obj.marketPrice;
            var goodSrc = obj.picUrl;
            this.$http.get("http://bxu2359550159.my3w.com/wochuphp/addshoppingcart.php?userId="+userId+"&goodId="+goodId+"&goodName="+goodName+"&num="+num+"&goodPrice="+goodPrice+"&oldPrice="+oldPrice+"&goodSrc="+goodSrc).then(function(res){
                var t = res.body;
                var that = this;
                console.log(res.body);
                if(t){
                    this.alertState = true;
                    this.alertText = "提示：加入购物车成功";
                    setTimeout(function(){
                        that.alertState = false;
                    },1000)
                    // alert("提示：加入购物车成功");
                    that.numCount += num;
                }
            })

        }
    }
  },
  beforeRouteEnter: function (to, from, next) {
    next(function (vm) {
      vm.goodsId = to.query.goodsId;
      vm.tagName = to.query.tagName;
      for (var i = 0; i < List.data.length; i++) {
        if (vm.tagName == List.data[i].tagName) {
          vm.goodsList = List.data[i].items;
        }
      }
    })
  },
  components: {
    Banner
  },
  created:function(){
        var userName = localStorage.getItem("userName");
        this.userName = userName;
        if(!userName){
            this.numCount = 0;
        }else{
            //若有用户登录，则显示该用户的购物车数量
            var that = this;
            this.$http.get("http://bxu2359550159.my3w.com/wochuphp/myshoppingcart.php?userId="+this.userName).then(function(res){
                // console.log(res);
                //  res.body;//res.body返回的是是一个数组
                for(var i=0;i<res.body.length;i++){
                    that.numCount += res.body[i][3]*1
                }
                
            })
        }
    }
}
</script>
<style lang="less">
  .goodsBanner {
    width: 100%;
    .swiper-slide {
        height: 6.15rem;
        width: 100%;
        overflow: hidden;
        img {
            display: block;
            border: 0 none;
            width: 100%;
            height: auto;
        }
    }
}

// .btn{
//   width:100%;
//   position: fixed;
//   top:0;
//   left:0;
//   z-index: 6;
//   .backBtn{
//     float: left;
//     font-size: 0.7rem;
//     color:rgba(0, 0, 0, 0.3);
//     margin:0.1rem 0 0 0.34rem;
//   }
//   .shareBtn{
//     float: right;
//     font-size: 0.7rem;
//     color:rgba(0, 0, 0, 0.3);
//     margin:0.1rem 0.34rem 0 0;
//   }
// }
</style>

<style lang="less" scoped>
     body{position:relative;}
    .alertBox{
      padding:0.1rem 0.2rem;
      background: rgba(0, 0, 0, 0.7);
      color:white;
      font-size: 0.23rem;
      display: inline-block;
      position:absolute;
      top:7.5rem;
      left:50%;
      transform: translate(-50%,-50%);
      }
</style>



