<template>
    <div class="listId1">
        <div>
            <router-link :to="{name:'GoodsDetail',query:{goodsId:'569d6b87-36ad-4927-9589-b2fbccaa5d06'}}">
                <img src="https://wochu.oss-cn-hangzhou.aliyuncs.com/upload/0a55b621-387e-475e-bc4e-bfb5288023a8.jpg" />
            </router-link>
        </div>
        <ul class="clear">
            <router-link tag="li" v-if="item.parentId==1" v-for="(item,index) in list" to="" key="index">
                <a href="javascript:;">
                    <img :src="item.checkicon" />
                    <p>{{item.name}}</p>
                </a>
            </router-link>
        </ul>
    </div>
</template>
<script>
import listJson from "../../json/list.json";
export default {
    name: "listId2",
    data() {
        return {
            list: listJson.data
        }
    }
}
</script>
<style lang="less">
.listId1 {
    &>div a{
        display: inline-block;
        width:100%;
        img{
            border:0 auto;
            width:100%;
            height:auto;
        }
    }
}
</style>





