<template>
    <div class="listId3">
        <div>
            <router-link :to="{name:'GoodsDetail',query:{goodsId:'0f2b67a4-5a52-4570-8c85-0f57539dbc08'}}">
                <img src="https://wochu.oss-cn-hangzhou.aliyuncs.com/upload/7edfe44d-7eec-4f83-97d3-54c562f84a73.jpg" />
            </router-link>
        </div>
        <ul class="clear">
            <router-link tag="li" v-if="item.parentId==4" v-for="(item,index) in list" to="" key="index">
                <a href="javascript:;">
                    <img :src="item.checkicon" />
                    <p>{{item.name}}</p>
                </a>
            </router-link>
        </ul>
    </div>
</template>
<script>
import listJson from "../../json/list.json";
export default {
    name: "listId2",
    data() {
        return {
            list: listJson.data
        }
    }
}
</script>
<style lang="less">
.listId3 {
    &>div a{
        display: inline-block;
        width:100%;
        img{
            border:0 auto;
            width:100%;
            height:auto;
        }
    }
}
</style>





