<template>
    <div>
        <Top></Top>
        <div class="main">
            <component :is="bannerCom" :bannerList="bannerList" :swiperClass="swiperClass"></component>
            <HomeContainer :recommendedContent="recommendedContent"></HomeContainer>
        </div>
        <Bottom></Bottom>
    </div>
</template>
<script>
import Top from "./com/Header";
import Banner from "./com/Banner";
import Bottom from "./com/Footer";
import HomeContainer from "./com/HomeContainer";
var list = require("../json/banner");
export default {
    name: "home",
    data: function () {
        return {
            bannerCom: "Banner",
            swiperClass:"homeBanner",
            // bannerList: [],//动态获取，$http插件进行请求 npm i vue-resource -S 在main.js中import Resource from "vue-resource";//ajax请求的插件 因为jquery插件容量太大Vue.use(Resource);
            // recommendedContent: []//动态获取
            bannerList:list.carousel,
            recommendedContent:list.recommendedContent
        }
    },
    components: {
        Top,
        Bottom,
        Banner,
        HomeContainer
    },
    // created: function () {
    //     var that = this;
    //     this.$http.get("/api/carousel").then(function (res) {//获取的是本地接口，传到网上后，会提示不能抓取到数据
    //         this.bannerList = res.body.data;
    //     })
    //     this.$http.get("/api/recommendedContent").then(function (res) {
    //         this.recommendedContent = res.body.data;
    //     })
    // }
}
</script>



