<template>
    <div>
        <div class="reglog_header">
            <h3>注册</h3>
            <i class="iconfont icon-back" @click="goBack"></i>
        </div>
        <div class="reg_main">
            <div>
                <input type="text" placeholder="输入手机号" ref="userName" maxlength="11" @blur="alertFun" />
            </div>
            <div>
                <input v-if="state==false" type="password" placeholder="密码由6-20位字符组成" ref="pwd" maxlength="20" @blur="alertFun" />
                <input v-else type="text" placeholder="密码由6-20位字符组成" ref="pwd" maxlength="20" @blur="alertFun"/>
                <button @click="pwdFun">{{pwdBtn}}</button>
            </div>
            <div>
                <input type="text" placeholder="输入验证码" ref="yzm" maxlength="8" @blur="alertFun"/>
                <button>获取验证码</button>
            </div>
            <div class="invitation">
                <input type="text" placeholder="输入邀请码（没有可跳过）" ref="yqm" maxlength="8" @blur="alertFun"/>
            </div>
            <div class="regBtn" >
                <a @click="toFun" href="javascript:;">注册并登录</a>
            </div>
            <p>注册即您已同意
                <a>服务协议</a>
            </p>
        </div>
        <div class="alertBox" ref="alertBox" v-show="alertState">{{alertText}}</div>
    </div>
</template>
<script>
import VueRouter from "vue-router";
var router = new VueRouter();
export default {
    name: "register",
    data() {
        return {
            pwdBtn: "密码显示",
            state: false,
            alertText: null,//警告框的文本内容
            alertState:false//警告框显示隐藏状态
        }
    },
    methods: {
        pwdFun() {
            if (this.state) {
                this.pwdBtn = "密码显示";
                this.state = false;
            } else {
                this.pwdBtn = "密码隐藏";
                this.state = true;
            }
        },
        goBack() {
            history.go(-1);
        },
        alertFun() {
            // alert(this.$refs.userName.value);//有效果
            var user = this.$refs.userName.value;
            var pwd = this.$refs.pwd.value;
            var yzm = this.$refs.yzm.value;
            var yqm = this.$refs.yqm.value;
            var that = this;
            if (!(/^1[34578]\d{9}$/.test(user))) {
                // this.$refs.alertBox.show().delay(3000).fadeOut();
                this.alertText = "手机号输入错误";
                this.alertState = true;
                setTimeout(function(){
                    that.alertState = false;
                },2000)
            } else if (!(/^[A-Za-z0-9]{6,20}$/).test(pwd)) {
                // this.$refs.alertBox.show().delay(3000).fadeOut();
                this.alertState = true;
                this.alertText = "请输入有效密码，6-20位密码，字母、数字或符号";
                setTimeout(function(){
                    that.alertState = false;
                },2000)
            }else if(yzm==""){
                this.alertState = true;
                this.alertText = "请输入验证码";
                setTimeout(function(){
                    that.alertState = false;
                },2000)
            }
        },
        toFun(){
            var user = this.$refs.userName.value;
            var pwd = this.$refs.pwd.value;
            var yzm = this.$refs.yzm.value;
            var yqm = this.$refs.yqm.value;
            var that = this;
            if(!user==""&&!pwd==""&&!yzm==""){
                // localStorage.setItem("userName",user);
                this.$http.get("http://bxu2359550159.my3w.com/wochuphp/zhuce.php?username="+user+"&pwd="+pwd).then(function(res){
                    //console.log(res.body);
                    var t= res.body;
                    if(t == 2){
                        this.alertState = true;
                        this.alertText = "该用户已存在";
                        setTimeout(function(){
                            that.alertState = false;
                        },2000)
                    }else if(!t){
                        this.alertState = true;
                        this.alertText = "数据库维修中，注册失败";
                        setTimeout(function(){
                            that.alertState = false;
                        },2000)
                    }else{
                        this.alertState = true;
                        this.alertText = "注册成功，请稍后";
                        setTimeout(function(){
                            that.alertState = false;
                            router.push({
                                path:"/my",
                                query:{userName:user}
                            });
                            localStorage.setItem("userName",user);
                        },2000)
                    }
                })
            }
        }
    }
}
</script>
