<template>
    <div class="listId2">
        <div>
            <router-link :to="{name:'GoodsDetail',query:{goodsId:'ff45adc4-4d80-4540-8d7f-4b97361ea855'}}">
                <img src="https://wochu.oss-cn-hangzhou.aliyuncs.com/upload/abae4276-e870-4475-9602-0302f95061ae.jpg" />
            </router-link>
        </div>
        <ul class="clear">
            <router-link tag="li" v-if="item.parentId==2" v-for="(item,index) in list" to="" key="index">
                <a href="javascript:;">
                    <img :src="item.checkicon" />
                    <p>{{item.name}}</p>
                </a>
            </router-link>
        </ul>
    </div>
</template>
<script>
import listJson from "../../json/list.json";
export default {
    name: "listId2",
    data() {
        return {
            list: listJson.data
        }
    }
}
</script>
<style lang="less">
.listId2 {
    &>div a{
        display: inline-block;
        width:100%;
        img{
            border:0 auto;
            width:100%;
            height:auto;
        }
    }
}
</style>





