<template>
    <div>
        <div class="listDetail_Header">
            <a href="javascript:;" class="i_left iconfont icon-back" @click="backFun"></a>
            <p>{{tagName}}</p>
            <router-link class="i_right iconfont icon-gouwuche" to="/cart"><span>{{numCount}}</span></router-link>
        </div>
        <div class="listDetail_main" v-for="(item,index) in list" v-if="item.tagName == tagName" :key="index">
            <ul>
                <router-link tag="li" :to="{name:'GoodsDetail',query:{tagName:tagName,goodsId:obj.goodsGuid}}" v-for="(obj,i) in item.items" :key="i">
                    <a href="javascript:;">
                        <div class="img-box">
                            <img :src="obj.picUrl"/>
                        </div>
                        <div class="content-box">
                            <h3>{{obj.goodsName}}</h3>
                            <p><span>￥{{obj.price}}</span><del>￥{{obj.marketPrice}}</del><i class="iconfont icon-jia" @click.stop.prevent="inCart(obj)"></i></p>
                        </div>
                    </a>
                </router-link>
            </ul>
        </div>
        <div class="alertBox" ref="alertBox" v-show="alertState">{{alertText}}</div>
    </div>
</template>
<script>
import fruitJson from "../json/fruit.json";
import VueRouter from "vue-router";
var router = new VueRouter();
export default {
    name: "listDetail",
    data() {
        return {
            tagName: null,
            list:fruitJson.data,
            alertText: null,//警告框的文本内容
            alertState:false,//警告框显示隐藏状态
            userName:null,
            numCount:0
        }
    },
    methods:{
        backFun(){
            // console.log("点击效果")
            // router.go(-1);//router is not defined
            window.history.go(-1);
        },
        inCart(obj){
            var userName = localStorage.getItem("userName");
            if(!userName){
                router.push({
                    path:"/login",
                    query:{redirect:'/listDetail?tagName='+this.tagName}
                });
            }else{
                // alert("加入购物车成功");
                var userId = userName;
                var goodId = obj.goodsGuid;
                var goodName = obj.goodsName;
                var num = 1;
                var goodPrice = obj.price;
                var oldPrice = obj.marketPrice;
                var goodSrc = obj.picUrl;
                this.$http.get("http://bxu2359550159.my3w.com/wochuphp/addshoppingcart.php?userId="+userId+"&goodId="+goodId+"&goodName="+goodName+"&num="+num+"&goodPrice="+goodPrice+"&oldPrice="+oldPrice+"&goodSrc="+goodSrc).then(function(res){
                    var t = res.body;
                    var that = this;
                    // console.log(res.body);
                    if(t){
                        this.alertState = true;
                        this.alertText = "提示：加入购物车成功";
                        setTimeout(function(){
                            that.alertState = false;
                        },1000)
                        that.numCount += 1;
                    }
                })
            }
        }
    },
    beforeRouteEnter: function (to, from, next) {
        //   this.tagName = to.query.tagName;//此处不能用this点变量名，使用钩子函数用到this时要使用next(function(vm){vm.tagName=...})
        //   console.log(to.query.tagName);
        next(function (vm) {
            vm.tagName = to.query.tagName;
        })
    },
    created:function(){
        var userName = localStorage.getItem("userName");
        this.userName = userName;
        if(!userName){
            this.numCount = 0;
        }else{
            //若有用户登录，则显示该用户的购物车数量
            var that = this;
            this.$http.get("http://bxu2359550159.my3w.com/wochuphp/myshoppingcart.php?userId="+this.userName).then(function(res){
                // console.log(res);
                //  res.body;//res.body返回的是是一个数组
                for(var i=0;i<res.body.length;i++){
                    that.numCount += res.body[i][3]*1
                }
                
            })
        }
    }
}
</script>


 


