<template>
    <div class="tj">
        <div v-for="(item,index) in list" :key="index" class="tjDiv">
            <h3>{{item.name}}</h3>
            <ul class="clear">
                <router-link tag="li" v-if="obj.url.length>10" v-for="(obj,index) in item.children" :to="{name:'IframeClick',query:{src:obj.url}}" :key="index">
                    <a href="javascript:;">
                        <img :src="obj.imgUrl" />
                        <p>{{obj.name}}</p>
                    </a>
                </router-link>
                <router-link tag="li" v-if="obj.url.length<10" v-for="(obj,index) in item.children" :to="{name:'ListDetail',query:{tagName:obj.url}}" :key="index">
                    <a href="javascript:;">
                        <img :src="obj.imgUrl" />
                        <p>{{obj.name}}</p>
                    </a>
                </router-link>
            </ul>
        </div>
    </div>
</template>
<script>
import tjList from "../../json/recommend.json";
export default {
    name: "listTj",
    data() {
        return {
            list: tjList.data
        }
    }
}
</script>
