.clear {
    content: ".";
    clear: both;
    display: block;
    height: 0;
    overflow: hidden;
}

.tj {
    padding-bottom: 0.98rem;
}

.tjDiv {
    h3 {
        font-weight: normal;
        font-size: 0.26rem;
        width: 100%;
    }
