<template>
    <div :class="'swiper-container ' | classFilter(swiperClass)">
        <div class="swiper-wrapper">
            <BannerItem v-for="(goods , index) in bannerList" :goods="goods" :key="index"></BannerItem>
        </div>
        <div class="swiper-pagination"></div>
    </div>
</template>
<script>
import Swiper from "swiper";
import BannerItem from "./BannerItem";
export default {
    name: "banner",
    props: {
        bannerList: {
            type: Array,
            required: true
        },
        swiperClass: {
            type: String
        }
    },
    data() {
        return {
            swiperObj: null
        }
    },
    filters: {
        classFilter: function (input, item) {
            return input + item;
        }
    },
    components: {
        BannerItem
    },
    mounted: function () {
        var className = ".swiper-container";
        if (this.swiperClass) className = "." + this.swiperClass;
        // console.log(className);
        this.swiperObj = new Swiper(className, {
            pagination: ".swiper-pagination",
            autoplay: 2000,
            loop: true
        })
    },
    updated: function () {
        this.swiperObj.update();
    }
}
</script>
<style src="swiper/dist/css/swiper.css"></style>
<style lang="less">
.homeBanner {
    width: 100%;
    .swiper-slide {
        height: 2.5rem;
        width: 100%;
        overflow: hidden;
        img {
            display: block;
            border: 0 none;
            width: 100%;
            height: auto;
        }
    }
}
</style>




